import React from 'react';

interface ServiceItemProps {
  title: string;
  description: string;
  image: string;
  imageDivClassOrder: string;
}

interface ServiceItemImageProps {
  image: string;
  title: string;
}

function ServiceItemImage({ image, title }: ServiceItemImageProps) {
  return <img className="rounded-2xl shadow-2xl" src={image} alt={title} />;
}

export default function ServiceItem({
  title,
  description,
  image,
  imageDivClassOrder,
}: ServiceItemProps) {
  return (
    <div className="grid md:grid-cols-2 sm:grid-cols-1 gap-4 p-4 mt-5">
      <div>
        <h1 className="m-4 text-3xl">{title}</h1>
        <p className="m-4">{description}</p>
        <button
          type="button"
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 m-4 rounded-xl shadow-xl transition ease-in-out delay-150 hover:-translate-y-1 hover:scale-110"
        >
          See it in action
        </button>
      </div>
      <div className={imageDivClassOrder}>
        <ServiceItemImage image={image} title={title} />
      </div>
    </div>
  );
}
